@media (max-width: 991px) {
  .img-main {
    margin-top: 140px;
    width: 240px;
    height: 240px;
  }
}
@media (max-width: 600px) {
  .main-header {
    flex-direction: column;
    font-size: 0.9em;
    height: auto;
    padding: 10px;

    h1 {
      // font-size: 2.5em;
      display: none;
    }
  }

}

@media (max-width: 450px) {
  .main-header > nav {
    flex-direction: column;
    width: 100%;

    a,
    span {
      border: 1px solid $charcoal;
      margin-bottom: 5px;
      text-align: center;
      width: 80%;
    }


  }
}

.contact-us {
  background-color: #00000050;
  border-radius: 40px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  place-content: space-evenly;

  p {
    font-size: 1.4em;
    font-family: monospace;
    margin: 0 15px;
  }
}